.noticeMain {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  .link {
    color: var(--color-heading-1);
    width: 40%;
    border: 1px solid #00684A;
    border-radius: 4px;
    padding: 10px 25px 10px 25px;
    background-color: var(--color-bg-4);
  }
  .iconStyle {
    display: flex;
    margin-bottom: 30px;
    height: 150px;
    width: 150px;
    color: #a2a2a2;
  }
}




