.labelMain {
  display: flex;
  align-items: center;
  user-select: none;
  cursor: pointer;
}
.checkbox {
  width: 25px;
  height: 25px;
  margin-right: 5px;
  -webkit-appearance: none;
  -moz-animation: none;
  appearance: none;
  background-image: url("./img/off.svg");
  cursor: pointer;
}

.checkbox:checked {
  cursor: pointer;
  -webkit-appearance: none;
  -moz-animation: none;
  appearance: none;
  background-image: url("./img/on.svg");
}

