.inputPassword {
  position: relative;
  display: inline-block;
  margin: 5px 0;
}

.superInput {
  font-size: 16px;
  height: 25px;
  padding: 5px;
  background: #f6f6f6;
  border: 1px solid #9f9f9f;
  border-radius: 4px;
}

.superInput:focus {
  background: white;
  box-shadow: 0 0 10px #9f9f9f;
  outline: none;
}

.errorInput {
  border: 2px solid red;
}

.error {
  color: red;
}

.eyeButton {
  position: absolute;
  top: 0;
  right: 5px;
  height: 100%;
  padding-top: 6px;
  cursor: pointer;
}

input:-webkit-autofill,
input:-webkit-autofill:focus {
  transition: background-color 600000s 0s, color 600000s 0s;
}

